import React, { useEffect, useState } from "react";
import { Card, Col, Row, CardBody, CardTitle, Table, Button } from "reactstrap";
import { useDispatch, useSelector } from "react-redux";
import { getAllUsers } from "../../redux/actions/userAction";
import { COLORS } from "../../colors";
import { IMAGES } from "../../constants";
import AvatarImage from "../../assets/images2/avatar.png";

import { store } from "../../redux/Store";
import { baseURL, imgURL } from "../../redux/config";
import axios from "axios";
import { toast } from "react-toastify";





const User = () => {

  const dispatch = useDispatch();
  const [userList, setuserList] = useState([]);
  const customerData = useSelector((state) => state.user.customer);
  const [selectedDetails, setselectedDetails] = useState(null);
  // console.log(customerData)

  let authData = { ...store.getState().auth.authData };
  let headers = { Authorization: "Bearer " + authData.accessToken };




  const disableUser = (item, data, index) => {


    axios.post(`${baseURL}user/updatestatus?id=${item._id}`, data, { headers })
      .then(res => {


        if (res.data?.data) {
          // dispatch(getAllUsers());
          let temp = userList;
          temp[index].userStatus = "disable"
          setuserList(temp)
          setuserList([...userList])
          toast.success("User Disabled")

        }

      })

      .catch(err => { console.log(err); toast.error('Network Error') })


  }





  const activeUser = (item, data, index) => {


    axios.post(`${baseURL}user/updatestatus?id=${item._id}`, data, { headers })
      .then(res => {


        if (res.data?.data) {
          // dispatch(getAllUsers());
          let temp = userList;
          temp[index].userStatus = "active"
          setuserList(temp)
          setuserList([...userList])

          toast.success("User Activated")

        }

      })

      .catch(err => { console.log(err); toast.error('Network Error') })


  }






  const styles = {
    tagstyle: {
      backgroundColor: COLORS.primary06,
      color: "white",
      marginRight: 2,
      marginBottom: 5,
      marginLeft: 5,
      borderRadius: 100,
      // padding:3,
      // paddingLeft:20,
      // paddingRight:20,
      display: "flex",
      textTransform: "capitalize",
      textAlign: "center",
      alignItems: "center",
      justifyContent: "center",
      // maxWidth:130
    },
  };

  useEffect(() => {
    if (!customerData.length) {
      dispatch(getAllUsers());
    } else {
      if (customerData.length !== userList.length) {
        setuserList(customerData);
      }
    }
  }, [customerData, dispatch]);

  // console.log("users", userList)



  const userProfileDetails = (data) => (
    <>
      {/* <CardBody className=""> */}

      <h3>Personal Details</h3>
      <Row style={{ height: 100 }}>
        <img
          src={data.image ? { uri: data.image } : AvatarImage}
          style={{ width: 100, height: 100, resize: "contain" }}
        />
        <span
          style={{
            backgroundColor:
              selectedDetails.userStatus === "active" ? "green" : "gray",
            marginTop: 70,
            marginLeft: 80,
            position: "absolute",
            zIndex: 10,
            borderRadius: 100,
            width: 20,
            height: 20,
          }}
        />
      </Row>
      <Row>
        <Col sm="3" style={{ fontWeight: "500" }}>
          Status
        </Col>
        <Col sm="6">{selectedDetails.userStatus}</Col>
      </Row>

      <Row>
        <Col sm="3" style={{ fontWeight: "500" }}>
          First Name
        </Col>
        <Col sm="6">{data.first_name}</Col>
      </Row>
      <Row>
        <Col sm="3" style={{ fontWeight: "500" }}>
          Last Name
        </Col>
        <Col sm="6">{data.last_name}</Col>
      </Row>
      <Row>
        <Col sm="3" style={{ fontWeight: "500" }}>
          Email
        </Col>
        <Col sm="6">{data.email}</Col>
      </Row>
      <Row>
        <Col sm="3" style={{ fontWeight: "500" }}>
          Phone
        </Col>
        <Col sm="6">{data.phone}</Col>
      </Row>
      <br />

      {/* </CardBody> */}
    </>
  );



  const renderSelectedUser = () => {
    let data = selectedDetails.profile;
    return (
      <>
        <Card>
          <CardTitle
            className="border-bottom p-3 mb-0"
            style={{
              display: "flex",
              flexDirection: "row",
              justifyContent: "space-between",
            }}
          >
            <div>
              <i className="mdi mdi-border-none mr-2"> </i>
              {data.first_name + " " + data.last_name} | Customers Details
            </div>
            <Button
              className="btn btn-primary"
              style={{
                backgroundColor: COLORS.black,
                borderColor: COLORS.black,
                borderRadius: 4,
                alignItems: "center",
              }}
              onClick={() => setselectedDetails(null)}
            >
              <i className="fa fa-arrow-left mr-2"> </i>Back
            </Button>
          </CardTitle>
          <CardBody>{userProfileDetails(data)}</CardBody>

          {/* <Row> */}

          {/* <Col sm="6" > */}
          {/* {userProfileDetails(data)} */}
          {/* </Col> */}

          {/* <Col sm="6">
    
      {userPurchasedPackages(data)}
    
  </Col> */}
          {/* </Row> */}
          {/* <Row>
      {/* <Col sm="12">
       {userSessions()}
      </Col> 
    </Row> */}
        </Card>
      </>
    );
  };




  const renderUserList = () => (
    <Card>
      <CardTitle className="bg-light border-bottom p-3 mb-0">
        <i className="mdi mdi-border-none mr-2"> </i>
        Customers
      </CardTitle>

      <CardBody className="" style={{ fontSize: 12 }}>
        <Table
          striped
          hover
          responsive
          condensed
          search={true}
          deleteRow={true}
          pagination
          tableHeaderClass="mb-0"
        >
          <thead>
            <tr style={{ textAlign: "center" }}>
              <th>Sr No#</th>
              <th>Photo</th>
              <th>Name</th>
              <th>Email</th>
              <th>Phone</th>
              <th>Status</th>
              <th colSpan={2}>Action</th>
            </tr>
          </thead>

          <tbody className="responsive">

            {



              userList.length > 0 ?
                userList[0].profile ?


                  userList && userList.map((item, index) => (

                    <tr style={{ textAlign: "center", height: 60 }}>
                      <th scope="row">{index + 1}</th>
                      <td>
                        <img
                          src={
                            item.profile?.image
                              ? imgURL + item.profile.image
                              : AvatarImage
                          }
                          alt="user"
                          style={{
                            width: 40,
                            height: 40,
                            marginTop: -5,
                            resize: "contain",
                          }}
                        />


                      </td>



                      <td>{console.log(item.profile)}

                        {item.profile?.length > 0 && item.profile?.first_name} {item && item.profile?.last_name}{" "}

                      </td>

                      <td>{item.profile?.email}</td>
                      <td>{item.profile?.phone}</td>
                      <td>{item.userStatus}</td>


                      <td>

                        {item.userStatus == "active" ?

                          <Button
                            onClick={() => disableUser(item, { userStatus: "disable" }, index)}
                            className="btn btn-info"
                            style={{
                              fontSize: 12,
                              backgroundColor:

                                COLORS.primary,
                              // : COLORS.black,
                              borderColor:
                                // item.userStatus == "active"
                                // ? COLORS.primary
                                COLORS.black,
                              width: "80px",
                            }}
                          >
                            {"Disable"}

                          </Button>

                          :

                          <Button
                            onClick={() => activeUser(item, { userStatus: "active" }, index)}
                            className="btn btn-info"
                            style={{
                              fontSize: 12,
                              backgroundColor:
                                // item.userStatus == "active"
                                // ? COLORS.primary
                                COLORS.black,
                              borderColor:
                                // item.userStatus == "active"
                                COLORS.primary,
                              // : COLORS.black,
                              width: "80px",
                            }}
                          >
                            {"Active"}

                          </Button>

                        }
                        <Button
                          onClick={() => setselectedDetails(item)}
                          style={{
                            width: "60px",
                            fontSize: 12,
                            backgroundColor: COLORS.black06,
                            borderColor: COLORS.black06,
                          }}
                          className="btn btn-info ml-3"
                        >
                          View
                        </Button>
                      </td>
                    </tr>
                  ))

                  :


                  console.log() : console.log()


            }


          </tbody>
        </Table>
      </CardBody>
    </Card>
  );






  return selectedDetails ? renderSelectedUser() : renderUserList();
};

export default User;
